import { CacheServiceInterface } from './cache-service.interface';

export class ArrayCacheService implements CacheServiceInterface {
  #cache: { [key: string]: any } = {};

  public get<T>(key: string): Promise<T | undefined> {
    return Promise.resolve(this.#cache[key] ?? undefined);
  }

  public set<T>(key: string, value: T): Promise<void> {
    this.#cache[key] = value;

    return Promise.resolve();
  }

  public clear(): Promise<void> {
    this.#cache = [];

    return Promise.resolve();
  }
}
